.App {
  width: 60%;
  margin: 0 auto;
}


@media (max-width:786px) {
  .App{
    width: 100%;
    margin: 0;
  }
  .controls-wrapper{
    flex-direction: column !important;
  }
}

.gradient-box {
  width: auto;
  height: auto;
  padding: 40px;
  box-shadow: 0px 1px 20px 3px rgb(0 0 0 / 56%);
  -webkit-box-shadow: 0px 1px 20px 3px rgb(0 0 0 / 56%);
  -moz-box-shadow: 0px 1px 20px 3px rgb(0 0 0 / 56%);
}

#bar {
  text-align: center;
  height: 25px;
  background-color: #dad9d9;
  margin: 0 auto;
  font-family: monospace;
  float: none;
  border-top-left-radius: 6px;
  border-top-right-radius: 6px;
  position: relative;
}

#red {
  background-color: #e94b35;
  border-radius: 100%;
  width: 12px;
  height: 12px;
  margin: 0 auto;
  left: 10px;
  top: 6px;
  /* bottom: -15%; */
  position: absolute;
}

#yellow {
  background-color: #777710;
  border-radius: 100%;
  width: 12px;
  height: 12px;
  margin: 0 auto;
  left: 26px;
  top: 6px;
  position: absolute;
  display: block;
}

#green {
  background-color: #1aaf5c;
  border-radius: 100%;
  width: 12px;
  height: 12px;
  margin: 0 auto;
  left: 42px;
  top: 6px;
  position: absolute;
  display: block;
}

#name input {
  height: 15px;
      margin: 0 auto;
      left: 0%;
      top: 4px;
      z-index: 99;
      position: relative;
      outline: none;
      background-color: #dad9d9;
      border: none;
      text-align: center;
}

.CodeMirror-scroll {
  min-height: 150px;
  margin-right: -30px;
  height: 100%;
  outline: none;
  position: relative;
  overflow: unset !important;
}

.CodeMirror-sizer {
  margin-bottom: 0px !important;
}

.CodeMirror-vscrollbar {
  display: none !important;
}

.CodeMirror {
  border-radius: 0px;
  border-bottom-right-radius: 6px;
  border-bottom-left-radius: 6px;
}

#code-wrapper {
  box-shadow: 2px 5px 32px -4px rgba(0, 0, 0, 0.56);
  -webkit-box-shadow: 2px 5px 32px -4px rgba(0, 0, 0, 0.56);
  -moz-box-shadow: 2px 5px 32px -4px rgba(0, 0, 0, 0.56);
  overflow: hidden;
}

.controls-wrapper {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
}

.controls-wrapper .colors__wrapper {
  overflow: hidden;
  width: 30px;
  height: 30px;
  border-radius: 100%;
  display: inline-block;
  margin: 0 5px;
  box-shadow: 0px 0px 13px 1px #3d4043;
}

.colors__wrapper input[type="color"] {
  -webkit-appearance: none;
  -moz-appearance: none;
  border: none;
  width: 100%;
  height: 100%;
  padding: 0;
}

.control__label{
  margin: 5px 0;
  text-align: center;
}

.colors__wrapper input[type="color"]::-webkit-color-swatch-wrapper {
  padding: 0;
}

.colors__wrapper input[type="color"]::-moz-color-swatch-wrapper {
  padding: 0;
}

.colors__wrapper input[type="color"]::-webkit-color-swatch {
  border: none;
}

.colors__wrapper input[type="color"]::-moz-color-swatch {
  border: none;
}

.custom-select {
  /* -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none; */
  height: 30px;
  background-size: 10px;
  transition: border-color .1s ease-in-out, box-shadow .1s ease-in-out;
  border: 1px solid #333;
  border-radius: 3px;
  max-width: 250px;
}

.custom-select:hover {
  border: 1px solid #999;
}

.custom-select:focus {
  border: 1px solid #999;
  box-shadow: 0 3px 5px 0 rgba(0, 0, 0, .2);
  outline: none;
}

/* remove default arrow in IE */
select::-ms-expand {
  display: none;
}

.custom-select {
  font-family: "Source Sans Pro", sans-serif;
  font-size: 1rem;
}

.download__wrapper {
  width: 50px;
  height: 50px;
  text-align: center;
  border-radius: 100%;
  overflow: hidden;
  background: #0D47A1;
  cursor: pointer;
  margin-top: 15px;
  transition: .2s ease-in-out 0s;
}

.download__wrapper:hover{
  box-shadow: 0px 4px 13px 1px black;
  transform: scale(1.25);
}

svg {
  overflow: visible;
  width: 20px;
  height: 20px;
  stroke: #B0BA23;
  stroke-width: 5;
  stroke-linecap: round;
  fill: none;
  margin-top: 13px;
}